.right-section-container {
	width: 70%;
	min-height: 100%;
	display: flex;
	flex-direction: column;
	padding: 0px 80px 1px 80px;
}
.next-days-container {
	width: 100%;
	display: flex;
	justify-content: space-between;
	gap: 28px;
}
.todays-highlights-container {
	width: 100%;
	height: 100%;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: repeat(2, 1fr);
	justify-content: center;
	align-content: center;
	justify-items: stretch;
	gap: 28px;
}
.next-days-title {
	font-size: 1.8rem;
	text-align: start;
	margin-bottom: 12px;
}
.todays-highlights-title {
	font-size: 1.8rem;
	text-align: start;
	margin-top: 28px;
	margin-bottom: 12px;
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
	.right-section-container {
		padding: 28px 24px;
	}
}
@media screen and (max-width: 850px) {
	.right-section-container {
		width: 100%;
	}
	.right-section-container {
		padding: 28px 24px;
	}
	.todays-highlights-title {
		text-align: center;
		margin-top: 52px;
		margin-bottom: 28px;
	}
	.next-days-title {
		text-align: center;
		margin-top: 52px;
		margin-bottom: 28px;
	}
	.todays-highlights-container {
		padding-bottom: 36px;
	}
}
@media screen and (max-width: 650px) {
	.next-days-container {
		gap: 24px;
		flex-wrap: wrap;
		justify-content: center;
	}
}
